import React from 'react';
import { Link } from 'react-router-dom';
import {
    Collapse,
    Navbar,
    NavbarToggler,
    NavbarBrand,
    Nav,
    NavLink,
 } from 'reactstrap';
  
  export default class Example extends React.Component {
    constructor(props) {
      super(props);
  
      this.toggle = this.toggle.bind(this);
      this.state = {
        isOpen: false
      };
    }
    toggle() {
      this.setState({
        isOpen: !this.state.isOpen
      });
    }
    render() {
      return (
        <div>
          <Navbar color="light" light expand="md">
            <NavbarBrand href="/"><img src="/assets/images/logo-nav-192x96.png" alt="TSN Cabinets"></img></NavbarBrand>
            <NavbarToggler onClick={this.toggle} />
            <Collapse isOpen={this.state.isOpen} navbar>
            <Nav className="ml-auto" navbar>
            <NavLink eventKey={1} componentClass={Link} href="/" to="/">HOME</NavLink>
            <NavLink eventKey={2} componentClass={Link} href="/services" to="/services">SERVICES</NavLink>
            <NavLink eventKey={3} componentClass={Link} href="/contact" to="/contact">CONTACT US</NavLink>

                
              </Nav>
            </Collapse>
          </Navbar>
        </div>
      );
    }
  }